import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service';
@Component({
  selector: 'app-facebook-redeem',
  templateUrl: './facebook-redeem.page.html',
  styleUrls: ['./facebook-redeem.page.scss'],
})
export class FacebookRedeemPage implements OnInit {
  data:any;
  points:any;
  currentColor:any;
  facebookCode:any;
  userid:any;
  response:any;
  constructor(public shared: HttpService) { }

  ngOnInit() {
  this.getPoints();
  }

  getPoints(){
    var profile = localStorage.getItem('profile');
    this.data=JSON.parse(profile);
    this.points=this.data.content.profile.points;
    this.userid=this.data.user_id;
  }


  update(){

   if (this.facebookCode == '' ||this.facebookCode ==undefined){

    this.shared.displayNetworkUpdate('Redeem code cannot be blank')
  }else{


    if (navigator.onLine) {
      this.shared.ServiceCallFacebook.ServiceCallRequestFacebookCode(this.userid,this.facebookCode).then((res) => { 
        this.response=res;
        // this.shared.displayNetworkUpdate(this.response.content)
        console.log(this.response);

if(this.response.message == 'error'){
  this.shared.displayNetworkUpdate(this.response.content);
}else{
  localStorage.setItem('profile', JSON.stringify(this.response));
  this.shared.displayNetworkUpdate("Successfully Redeem");
  this.getPoints();
}

        
        }, err => {
     
        })
  
    }else{
      this.shared.displayNetworkUpdate('No Internet Connection')
  
    }

  }
  }



}
